<template>
    <div style="padding: 1rem; margin: 1rem;">
        <div class="is--center">States</div>
        <div style="margin: auto; max-width: 600px;">
        <div class="normal-row2 is--center" style="padding: 1rem;">
            <a
                class="button is--template-border w-inline-block"
                @click="$router.push('/state')"
            >
                <div class="copy_code-w">
                    <div>Go To Create New State</div>
                </div>
            </a>
            <a class="button is--template-border w-inline-block" @click="fetchData()">
                refresh list
            </a>
        </div>
        </div>

        <div>
            <div class="little-row2">
                <state-comp v-for="(state, i) in states" :key="i" :state="state"></state-comp>
            </div>
        </div>
        <div class="wallpaper"></div>
    </div>
</template>

<script>
// import authHeader from '@/middleware/authHeader'

export default {
    name: "States",
    data: () => ({
        states: []
    }),
    components: {
        StateComp: () => import("../components/state-comp.vue"),
    },
    methods: {
        fetchData: async function () {
            const loader = this.$loading.show();
            try {
                const newUrl = new URL("https://x8ki-letl-twmt.n7.xano.io/api:9toeBRNq/states")
                const response = await this.$axios({ method: "get",  url: newUrl.pathname, baseURL: newUrl.origin})
                this.states = response.data;
                this.states.sort((a, b) => {
                    return a.id - b.id
                });
                localStorage.setItem('states', JSON.stringify(this.states));
            } catch (error) {
                console.log(error);
                alert(error);
                loader.hide();
            }
            loader.hide();
        }
    },
    mounted () {
        const states = localStorage.getItem('states');
        if (!states) {
            this.fetchData();
        } else {
            this.states = JSON.parse(states);
        }
    }
}
</script>

<style scoped>
.little-row2 {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}
</style>